// TESTIMONIALS

.testimonials {
  padding-top: 90px;
  padding-bottom: 143px;
  @include r(991) {
    padding-bottom: 90px; }
  @include r(767) {
    padding-top: 45px;
    padding-bottom: 45px; }


  .reviews & {
    padding-top: 48px;
    padding-bottom: 175px;
    @include r(767) {
      padding-top: 30px;
      padding-bottom: 80px;

      .swiper-wrapper {
        flex-wrap: wrap;
        height: auto !important; } } }


  &__title {
    padding-bottom: 58px; }


  &__btn {
    margin-top: 86px;
    @include r(767) {
      margin-top: 43px; } }

  &__block {
    white-space: normal;
    width: 388px;
    margin-bottom: 20px;
    padding: 35px 30px;
    border-radius: 3px;
    border: 1px solid $c-whisper;
    background-color: $c-white;
    @include transition(box-shadow);
    @include r(767) {
      .homepage & {
        flex: 0 0 300px;
        margin-right: 18px;
        margin-bottom: 50px; }

      position: static !important;
      width: calc((100% - 36px) / 2);
      margin-right: 18px;
      margin-bottom: 18px; }
    @include r(575) {
      width: 100%;
      margin-left: auto;
      margin-right: auto; }
    @include rmin(768) {
      display: block;
      position: absolute !important;
      margin-right: 0;
      width: calc((100% - 18px) / 2); }
    @include rmin(992) {
      width: calc((100% - 36px) / 3); }

    &:hover {
      box-shadow: 0 0 15px 0 rgba($c-blueZodiac, 0.1);
      @include transition(box-shadow);
      transition-duration: 0s; }

    &-header {
      display: flex; }


    &-body {
      padding-top: 25px; }


    &-left {
      width: 59px; }


    &-right {
      width: calc(100% - 59px);

      p {
        &:nth-child(1) {
          font-size: 20px;
          font-weight: 600; }
        &:nth-child(2) {
          font-style: italic; } } }

    &-icon {
      display: flex;
      align-items: flex-end;
      justify-content: center;
      width: 40px;
      height: 40px;
      text-align: center;
      border-radius: 100%;
      background-color: $c-royalBlue; } } }

// \ TESTIMONIALS
