// HEADER

.header {
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;

  &__bar {
    min-height: 40px;
    background-color: $c-blueZodiac;
    @include r(767) {
      display: none; }

    .nav__link,
    .lang__link,
    .phone__link {
      display: flex;
      align-items: center;
      justify-content: center;
      min-height: 40px;
      transition: color .15s $ease;

      &:hover,
      &.is-active {
        color: darken($c-white, 25%) !important;
        transition-duration: 0s; } }


    &-row {
      display: flex;
      align-items: center;
      justify-content: space-between;
      min-height: 40px; }


    &-left {
      display: flex;
      width: 130px; }


    &-middle {
      display: flex;
      justify-content: center;
      width: calc(100% - (130px + 130px)); }


    &-right {
      display: flex;
      justify-content: flex-end;
      width: 130px; } }


  &__wrapper {
    min-height: 107px;
    background-color: rgba($c-whiteSmoke, 0.95);
    @include r(767) {
      min-height: 90px; }


    &-row {
      display: flex;
      align-items: center;
      justify-content: space-between;
      min-height: 107px;
      @include r(767) {
        min-height: 90px; } }


    &-left {
      @include rmin(768) {
        width: 150px; }
      @include rmin(992) {
        width: 200px; } }


    &-middle {
      display: flex;
      justify-content: center;
      min-height: 107px;
      @include r(767) {
        display: none; }
      @include rmin(768) {
        width: calc(100% - (150px + 100px)); }
      @include rmin(992) {
        width: calc(100% - (200px * 2)); }

      .nav__link {
        overflow: hidden;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        min-height: 107px;
        transition: color .15s $ease;

        &:hover,
        &.is-active {
          color: lighten($c-royalBlue, 15%);
          transition-duration: 0s;

          &:before {
            transform: translateY(0);
            background-color: $c-royalBlue;
            transition-duration: 0s; } }

        &.is-active {
          color: $c-blueZodiac; }


        &:before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 3px;
          background-color: transparent;
          transform: translateY(-3px);
          transition: transform .15s $ease, background-color .15s $ease; } } }


    &-right {
      display: flex;
      justify-content: flex-end;
      min-height: 107px;
      @include r(767) {
        display: none; }
      @include rmin(768) {
        width: 100px; }
      @include rmin(992) {
        width: 200px; } }

    &-mobile {
      display: none;
      @include r(767) {
        display: flex;
        justify-content: flex-end; } } } }

// \ HEADER
