// PRINT

.print {
  padding-bottom: 90px;
  @include r(767) {
    padding-top: 120px;
    padding-bottom: 45px; }


  .press & {
    padding-top: 72px;
    padding-bottom: 133px;
    @include r(767) {
      padding-top: 35px;
      padding-bottom: 65px;

      .swiper-wrapper {
        flex-wrap: wrap;
        height: auto !important; } } }


  &__title {
    padding-top: 30px;
    padding-bottom: 50px; }


  &__btn {
    margin-top: 97px;
    @include r(767) {
      margin-top: 43px; }

    .press & {
      margin-top: 152px;
      @include r(767) {
        margin-top: 75px; }
      @include r(575) {
        margin-top: 36px; } } }


  &__block {
    white-space: normal;
    width: 388px;
    margin-bottom: 20px;
    padding: 35px 30px;
    border-radius: 3px;
    border: 1px solid $c-whisper;
    background-color: $c-white;
    @include transition(box-shadow);
    @include r(767) {
      .homepage & {
        flex: 0 0 300px;
        margin-right: 18px;
        margin-bottom: 50px; }

      position: static !important;
      width: calc((100% - 36px) / 2);
      margin-right: 18px;
      margin-bottom: 18px; }
    @include r(575) {
      width: 100%;
      margin-left: auto;
      margin-right: auto; }
    @include rmin(768) {
      display: block;
      position: absolute !important;
      margin-right: 0;
      width: calc((100% - 18px) / 2); }
    @include rmin(992) {
      width: calc((100% - 36px) / 3);

      &:hover {
        box-shadow: 0 0 15px 0 rgba($c-blueZodiac, 0.1);
        @include transition(box-shadow);
        transition-duration: 0s; } }

    &-header {
      padding-bottom: 32px; } } }

// \ PRINT
