.footer {
  padding-top: 43px;
  padding-bottom: 73px;
  background-color: $c-whiteSmoke;
  @include r(767) {
    padding-top: 32px;
    padding-bottom: 40px; }


  &__row {
    display: flex;
    @include r(767) {
      flex-direction: column; } }


  &__col {
    width: calc(100% / 3);
    @include r(767) {
      width: 100%; }

    &-0,
    &-1 {
      @include r(767) {
        padding-bottom: 55px; } } } }

