// VIDEO

.video {
  padding-top: 144px;
  padding-bottom: 291px;
  @include r(767) {
    padding-top: 72px;
    padding-bottom: 145px; }


  &__subtitle {
    width: 100%;
    max-width: 753px;
    padding-bottom: 72px;
    @include r(767) {
      padding-bottom: 36px; } }


  &__container {
    position: relative;
    background-color: $c-whiteSmoke;
    @include rmin(320) {
      height: 350px; }
    @include rmin(576) {
      height: 452px; }
    @include rmin(768) {
      height: 521px; }
    @include rmin(992) {
      height: 621px; }

    iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%; } } }


// \ VIDEO
