// INDEX

// MAIN TITLE
.title {
  font-weight: 500;
  @include rmin(320) {
    font-size: 30px; }
  @include rmin(1200) {
    font-size: 35px; }
  @include rmin(1440) {
    font-size: 40px; }
  @include rmin(1920) {
    font-size: 45px; }

  &--wrap {
    @include r(575) {
      font-size: 24px; } } }
// \ MAIN TITLE


// MAIN SUBTITLE
.subtitle {
  line-height: 1.15;
  font-weight: 400;
  @include rmin(320) {
    font-size: 20px; }
  @include rmin(768) {
    font-size: 22px; }
  @include rmin(992) {
    font-size: 24px; }
  @include rmin(1200) {
    font-size: 28px; }
  @include rmin(1440) {
    font-size: 32px; } }
// \ MAIN SUBTITLE


// CABINET TITLE
.cabinet__title {
  padding-bottom: 38px;
  font-size: 28px;
  font-weight: 600;
  @include r(767) {
    padding-bottom: 20px;
    font-size: 24px; } }
// \ CABINET TITLE


// BTN
.btn {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 251px;
  height: 50px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  color: $c-royalBlue;
  font-size: 18px;
  font-family: 'Montserrat', sans-serif;
  border-radius: 25px;
  border: 2px solid rgba($c-royalBlue, 0.5);
  background-color: transparent;
  @include transition(border-color, background-color, color);
  @include r(991) {
    font-size: 16px; }

  &:hover,
  &:focus {
    border-color: $c-royalBlue;
    @include transition(border-color);
    transition-duration: 0s; }

  &--big {
    max-width: 385px; }

  &--medium {
    max-width: 308px; }

  &--small {
    max-width: 285px; }

  &--esmall {
    max-width: 224px; }

  &--full {
    max-width: 100%; }

  &--color {
    border-color: $c-royalBlue;
    background-color: $c-royalBlue;
    color: $c-white;

    &:hover,
    &:focus {
      border-color: $c-ceruleanBlue;
      background-color: $c-ceruleanBlue;
      @include transition(border-color, background-color);
      transition-duration: 0s; } }

  &--colorW {
    border-color: $c-white;
    background-color: $c-white;
    color: $c-royalBlue;

    &:hover,
    &:focus {
      border-color: $c-white;
      background-color: $c-white;
      color: $c-blueZodiac;
      @include transition(color);
      transition-duration: 0s; } }

  &--colorWT {
    border-color: rgba($c-white, 0.5);
    background-color: transparent;
    color: $c-white;
    @include transition(border-color);

    &:hover,
    &:focus {
      border-color: $c-white;
      @include transition(border-color);
      transition-duration: 0s; } }

  &--red {
    border-color: #FF635C;
    background-color: transparent;
    color: #FF635C;
    @include transition(border-color);

    &:hover,
    &:focus {
      border-color: #FF635C;
      color: white;
      background-color: #FF635C;
      @include transition(border-color);
      transition-duration: 0s; } } }
// \ BTN


//- HAMBURGER
.hamburger {
  cursor: pointer;

  &__box {
    position: relative;
    width: 37px;
    height: 23px;

    &:before,
    &:after {
      content: '';
      position: absolute;
      left: 0;
      height: 5px;
      background-color: $c-royalBlue;
      @include transition(background-color);
      transition-duration: 0.25s;

      .is-active & {
        background-color: $c-LightSeaGreen;
        @include transition(width, background-color);
        transition-duration: 0s; } }

    &:before {
      top: 0;
      width: 100%; }

    &:after {
      bottom: 0;
      width: 35px;
      @include transition(width, background-color);
      transition-duration: 0.25s;

      .is-active & {
        width: 100%;
        @include transition(width, background-color);
        transition-duration: 0s; } }

    &-line {
      position: absolute;
      width: 31px;
      height: 5px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      background-color: $c-royalBlue;
      @include transition(width, background-color);
      transition-duration: 0.25s;

      .is-active & {
        width: 100%;
        background-color: $c-LightSeaGreen;
        @include transition(width, background-color);
        transition-duration: 0s; } } } }

//- \ HAMBURGER


// FORM
.form {
  &__field {
    position: relative;
    width: 100%;
    max-width: 346px;
    height: 52px;
    @include r(767) {
      margin-left: auto;
      margin-right: auto; }

    &--p {
      margin-bottom: 46px;
      @include r(767) {
        margin-bottom: 23px; } }

    &--date {
      .form__label {
        width: auto; }

      &:nth-child(1),
      &:nth-child(2) {
        .selectric-wrapper {
          width: 80px !important; } }
      &:nth-child(3) {
        .selectric-wrapper {
          width: 90px !important; } } }

    &-textarea {
      height: 155px;
      background-color: $c-whiteSmoke;
      border-bottom: 2px solid rgba($c-royalBlue, 0.5);
      @include transition(border-color);

      &:hover,
      &:focus {
        border-color: $c-royalBlue;
        @include transition(border-color);
        transition-duration: 0s; }

      &.has-error {
        border-color: $c-bittersweet; }

      .form__label {
        top: auto;
        bottom: 0;
        width: 100%;
        height: 52px; } }

    &--readonly {
      input {
        border-color: $c-nobel;

        &:hover,
        &:focus {
          border-color: $c-nobel;
          @include transition(border-color);
          transition-duration: 0s; } } }

    &--select {
      display: flex;
      align-items: center;
      border-bottom: 2px solid rgba($c-royalBlue, 0.5);
      @include transition(border-color);

      &.has-error {
        border-color: $c-bittersweet;

        &:hover,
        &:focus {
          border-color: $c-bittersweet; } }

      &:hover,
      &:focus {
        border-color: $c-royalBlue;
        @include transition(border-color);
        transition-duration: 0s; }

      .selectric {
        height: 100%;
        border: none;
        background-color: transparent;

        &-wrapper {
          height: 100%;
          flex: 1 1 auto; }

        &-items {
          top: calc(100% + 2px);
          border: none;
          background-color: $c-white;

          li {
            white-space: nowrap;
            cursor: pointer;
            border-bottom: 2px solid rgba($c-royalBlue, 0.5);
            background-color: $c-white;
            color: $c-blueZodiac;
            font-size: 18px;
            @include transition(border-color, background-color);
            @include r(767) {
              font-size: 16px; }

            &:last-child {
              border-bottom: none; }

            &:hover,
            &.selected {
              border-color: $c-royalBlue;
              background-color: $c-white;
              @include transition(border-color, background-color);
              transition-duration: 0s; } } }

        .label,
        .button {
          height: 100%; }

        .label {
          display: flex;
          align-items: center;
          margin-left: 0;
          line-height: normal;
          font-size: 18px;
          @include r(767) {
            font-size: 16px; } }

        .button {
          background-color: transparent;

          &:after {
            border-top-color: $c-blueZodiac; } } }

      .form__label {
        position: static;
        flex: 0 0 auto;
        width: auto; } } }

  &__input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding-left: 0;
    padding-right: 135px;
    border: none;
    border-radius: 0;
    box-shadow: none;
    border-bottom: 2px solid rgba($c-royalBlue, 0.5);
    background-color: transparent;
    font-size: 18px;
    font-family: 'Montserrat', sans-serif;
    @include transition(border-color);
    @include r(991) {
      font-size: 16px; }

    &:hover,
    &:focus {
      border-color: $c-royalBlue;
      @include transition(border-color);
      transition-duration: 0s; }

    .has-error & {
      border-color: $c-bittersweet; }

    &::-webkit-input-placeholder {
      color: rgba($c-blueZodiac, 0.5);
      font-size: 18px;
      @include r(991) {
        font-size: 16px; } }
    &::-moz-placeholder {
      color: rgba($c-blueZodiac, 0.5);
      font-size: 18px;
      @include r(991) {
        font-size: 16px; } }
    &:-ms-input-placeholder {
      color: rgba($c-blueZodiac, 0.5);
      font-size: 18px;
      @include r(991) {
        font-size: 16px; } }
    &:-moz-placeholder {
      color: rgba($c-blueZodiac, 0.5);
      font-size: 18px;
      @include r(991) {
        font-size: 16px; } } }

  &__textarea {
    resize: none;
    width: 100%;
    height: calc(155px - 52px);
    padding: 10px 0;
    border: none;
    background-color: transparent;
    font-size: 18px; }

  &__label {
    cursor: pointer;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    top: 0;
    right: 0;
    height: 100%;
    width: 135px;
    font-size: 12px;
    font-weight: 400;

    .has-error & {
      color: $c-bittersweet; } }


  &__btn {
    @include r(767) {
      margin-left: auto;
      margin-right: auto; } }


  &__info {
    font-size: 18px;
    @include r(991) {
      font-size: 16px; }
    @include r(767) {
      display: none;

      br {
        display: none; } }

    &--i {
      font-size: 14px;
      font-style: italic; } } }


// LOGO
.logo {
  &__link {
    img {
      max-width: 100%;
      @include rmin(320) {
 }        //height: 15px
      @include rmin(576) {
        height: auto; }
      @include rmin(768) {
        height: 25px; }
      @include rmin(992) {
        height: auto; } } } }
// \ LOGO


// PARLOR
.parlor {
  position: relative;
  white-space: nowrap;
  display: flex;
  align-items: center;
  justify-content: center;
  color: $c-royalBlue;
  @include transition(color);
  @include rmin(768) {
    font-size: 14px; }
  @include rmin(992) {
    font-size: 16px; }
  @include rmin(1200) {
    font-size: 18px; }

  &:hover {
    @include rmin(768) {
      color: lighten($c-royalBlue, 15%);
      @include transition(color);

      &:before {
        transform: translateY(0);
        background-color: $c-royalBlue;
        @include transition(transform, background-color);
        transition-duration: 0s; }

      i {
        opacity: 0.65;
        @include transition(opacity); } } }

  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: transparent;
    transform: translateY(-3px);
    @include transition(transform, background-color); }


  &__icon {
    margin-right: 5px;
    @include transition(opacity); }

  .icon-logout {
    margin-left: 10px; } }

// \ PARLOR


// PHONE
.phone {
  &.phone--footer {
    padding-bottom: 36px; }
  &.phone--header {
    .phone__link {
      color: $c-white; } }


  &__link {
    font-weight: 600;
    @include transition(color);

    &:hover {
      color: $c-royalBlue;
      @include transition(color);
      transition-duration: 0s; } } }
// \ PHONE


// DESIGN BY
.designBy {
  cursor: pointer;

  &:hover {
    a {
      color: $c-blueZodiac;
      @include transition(color);
      transition-duration: 0s; } }


  &__text {
    a {
      color: $c-royalBlue;
      @include transition(color); } } }
// \ DESIGN BY


// INPUT RANGE
input[type=range] {
  outline: none;
  cursor: pointer;
  width: 50%;
  height: 5px;
  margin: auto;
  padding: 0;
  background-color: rgba($c-blueZodiac, 0.3);
  background-image: linear-gradient($c-blueZodiac, $c-blueZodiac);
  background-size: 0 100%;
  background-repeat: no-repeat;
  -webkit-appearance: none; }

input[type=range]::-webkit-slider-runnable-track {
  box-shadow: none;
  border: none;
  background: transparent;
  -webkit-appearance: none; }

input[type=range]::-moz-range-track {
  box-shadow: none;
  border: none;
  background: transparent; }

input[type=range]::-moz-focus-outer {
  border: 0; }

input[type=range]::-webkit-slider-thumb {
  width: 26px;
  height: 26px;
  border: 4px solid $c-white;
  background: $c-royalBlue;
  border-radius: 100%;
  -webkit-appearance: none; }

input[type=range]::-moz-range-thumb {
  width: 26px;
  height: 26px;
  border: 4px solid $c-white;
  background: $c-royalBlue;
  border-radius: 100%; }
// \ INPUT RANGE


// INPUT ERROR
.error {
  display: none !important;
  position: absolute;
  left: 0;
  bottom: -22px;
  color: red;
  font-size: 10px;
  font-weight: 700;
  animation-fill-mode: both;
  animation-name: fadeIn;
  animation-duration: 0.5s;

  .modal & {
    bottom: -15px; } }
// \ INPUT ERROR


// SWIPER
@include r(767) {
  .homepage {
    .swiper-wrapper {
      align-items: flex-start; } }

  .swiper {
    &-container {
      padding-right: 20px;
      padding-left: 20px;
      margin-right: -20px;
      margin-left: -20px; }
    &-pagination {
      padding-right: 20px; } } }
// \ SWIPER
