// PARTNERS

.cabinet {
  .partners {
    padding-top: 73px;
    padding-bottom: 161px;
    @include r(767) {
      padding-top: 36px;
      padding-bottom: 80px; }


    &__container {
      &-0 {
        padding-bottom: 20px; }
      &-1 {
        padding-bottom: 100px;
        @include r(767) {
          padding-bottom: 50px; } }
      &-2 {
        padding-bottom: 48px; }
      &-3 {
        padding-bottom: 44px; } }


    &__row {
      display: flex;
      @include r(767) {
        flex-wrap: wrap; }

      &--center {
        align-items: center; }

      &-left,
      &-right {
        width: 50%;
        @include r(767) {
          width: 100%;
          margin-bottom: 20px; } } }


    &__pagination {
      &-list {
        display: flex;
        flex-wrap: wrap; }

      &-item {
        display: flex;
        align-items: center;
        padding-bottom: 10px;
        @include r(767) {
          &:nth-child(1) {
            width: 100%; } } }

      &-title {
        margin-right: 30px;
        font-size: 18px; }

      &-link {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 32px;
        border-radius: 3px;
        border: 1px solid rgba($c-royalBlue, 0.2);
        margin-right: 10px;
        padding-left: 10px;
        padding-right: 10px;
        color: $c-royalBlue;
        text-align: center;
        font-size: 18px;
        @include transition(border-color, background-color, color);
        @include r(767) {
          font-size: 16px; }

        &:hover {
          border-color: $c-royalBlue;
          @include transition(border-color); }
        &.is-active {
          border-color: transparent;
          background-color: rgba($c-blueZodiac, 0.24);
          color: $c-blueZodiac;
          @include transition(border-color, background-color, color); } } }



    &__banner {
      display: flex;
      align-items: flex-start;
      @include r(767) {
        flex-wrap: wrap; }

      &-left {
        width: 310px;
        @include r(767) {
          width: 100%;
          margin-bottom: 20px; }

        img {
          display: block;
          max-width: 100%;
          height: auto; } }

      &-right {
        width: calc(100% - 310px);
        padding: 20px 20px 30px 20px;
        border-radius: 3px;
        border: 1px solid $c-whisper;
        background-color: $c-whiteSmoke;
        @include r(767) {
          width: 100%; }

        p {
          color: $c-nobel;
          font-size: 14px;
          width: 100%;
          max-width: 500px; }

        a {
          margin: 0;
          margin-top: 41px; } } }



    &__block {
      display: flex;
      flex-direction: column;
      width: calc(50% - 10px);
      min-height: 311px;
      background-color: $c-whiteSmoke;
      @include r(767) {
        width: 100%;
        margin-bottom: 20px; }

      &:nth-child(odd) {
        @include rmin(768) {
          margin-right: 10px; } }
      &:nth-child(even) {
        @include rmin(768) {
          margin-left: 10px; } }

      &-0 {
        .partners__block-top {
          display: flex;
          flex-direction: column;
          justify-content: center;
          padding: 0 30px;
          background-color: $c-LightSeaGreen;
          @include r(575) {
            padding: 0 15px; }

          p {
            color: $c-white;
            &:nth-child(1) {
              padding-bottom: 10px;
              font-size: 14px;
              font-style: italic; }

            &:nth-child(2) {
              overflow: hidden;
              text-overflow: ellipsis;
              @include rmin(320) {
                font-size: 20px; }
              @include rmin(1200) {
                font-size: 24px; } } } }

        .partners__block-bottom {
          display: flex;
          align-items: flex-start;
          padding: 36px 0 0 30px;
          @include rmin(320) {
            padding: 36px 15px 0; }
          @include rmin(576) {
            padding-left: 30px; }
          @include rmin(992) {
            padding: 36px 0 0 30px; }

          a {
            margin: 0; } } }

      &-1 {
        .partners__block-top {
          padding-top: 48px;
          padding-left: 30px;
          @include r(575) {
            padding-right: 30px;
            padding-left: 15px; }

          img {
            display: block;
            max-width: 100%;
            height: auto; } }

        .partners__block-bottom {
          display: flex;
          align-items: center;
          padding: 0 30px;
          @include r(575) {
            padding: 0 15px; }

          p {
            font-size: 18px;
            @include r(767) {
              font-size: 16px; }
            span {
              font-weight: 700; } } } }


      &-top,
      &-bottom {
        height: 50%; } }



    &__table {
      padding-top: 16px;
      padding-bottom: 12px;
      background-color: $c-whiteSmoke;

      &-row {
        display: flex;
        min-height: 40px;
        padding-left: 30px;
        @include r(767) {
          flex-wrap: wrap;
          padding: 15px; }

        &-0 {
          margin-bottom: 12px;
          @include r(767) {
            min-height: 89px;
            margin: 0;
            border-bottom: 1px solid $c-white; }
          .partners__table-col {
            width: 50%;
            @include r(767) {
              align-items: flex-start;
              width: 100%;
              padding-top: 10px; }
            &:nth-child(1) {
              p {
                font-size: 18px; } }
            &:nth-child(2) {
              @include r(767) {
                display: none; }
              p {
                color: $c-nobel; } } } }

        &-1,
        &-2,
        &-3 {
          .partners__table-col {
            &:nth-child(1),
            &:nth-child(2) {
              width: 25%;
              @include r(767) {
                width: 50%;
                padding-bottom: 35px; } }

            @include r(767) {
              width: calc(100% / 3); }

            &--desktop {
              @include rmin(768) {
                width: calc((100% - 50%) / 3); } } } }

        &-1 {
          @include r(767) {
            display: none; }
          .partners__table-col {
            p {
              color: $c-nobel; } } }



        &-4,
        &-5 {
          .partners__table-col {
            &:nth-child(1) {
              width: 50%;
              @include r(767) {
                width: 100%;
                padding-bottom: 35px; }

              p {
                color: $c-nobel; } }

            @include r(767) {
              width: calc(100% / 3); }

            &--desktop {
              @include rmin(768) {
                width: calc((100% - 50%) / 3); }

              p {
                font-weight: 700; } } } }


        &-4 {
          margin-top: 25px; }


        &-1,
        &-2,
        &-3 {
          @include rmin(768) {
            border-bottom: 1px solid $c-white; } }

        &-2,
        &-3 {
          @include r(767) {
            border-bottom: 1px solid $c-white; } } }



      &-col {
        display: flex;
        align-items: center;
        @include r(767) {
          display: block; }

        &--mobile {
          display: none;
          @include r(767) {
            display: block; }

          p {
            color: $c-nobel;
            font-weight: 500 !important; } } } } } }

// \ PARTNERS
