// INVESTORS

.investors {
  position: relative;

  .calc {
    @include r(575) {
      .container {
        padding-left: 0;
        padding-right: 0; }
      .calc__title {
        padding-left: 15px;
        padding-right: 15px; } } }


  &__bg {
    @include r(767) {
      display: none; }

    &-top,
    &-bottom {
      z-index: 0;
      position: absolute;
      left: 0;
      width: 100%; }

    &-top {
      top: 0;
      height: 80%;
      background-color: $c-whiteSmoke; }

    &-bottom {
      bottom: 0;
      height: 20%;
      @include r(991) {
        //height: 15%
        background-color: $c-whiteSmoke; } } }



  .container {
    z-index: 1;
    position: relative;
    @include r(575) {
      .homepage & {
        padding-left: 0;
        padding-right: 0; } } }


  &__title {
    padding-bottom: 66px;
    @include rmin(768) {
      padding-bottom: 46px; }
    @include rmin(992) {
      padding-bottom: 66px; } }


  &__row {
    display: flex;
    @include rmin(320) {
      flex-direction: column-reverse; }
    @include rmin(768) {
      flex-direction: row; }

    &-left,
    &-right {
      width: 50%;
      @include r(767) {
        width: 100%; } }

    &-left {
      padding-bottom: 103px;
      @include r(575) {
        padding-left: 20px;
        padding-right: 20px; }
      @include rmin(768) {
        padding-top: 112px; }
      @include rmin(1200) {
        padding-top: 179px; } }

    &-right {
      @include rmin(768) {
        padding-left: 30px;
        padding-right: 30px; }
      @include rmin(992) {
        padding-left: 50px;
        padding-right: 50px; } } }


  &__col {
    display: flex;
    @include rmin(320) {
      flex-direction: column;
      padding-bottom: 30px; }
    @include rmin(768) {
      padding-bottom: 46px; }
    @include rmin(992) {
      flex-direction: row;
      padding-bottom: 56px; }

    &-left,
    &-right {
      width: 268px;
      @include r(767) {
        width: 100%; }
      @include rmin(320) {
        padding-bottom: 30px; }
      @include rmin(992) {
        margin-right: 36px; }

      p {
        font-size: 18px; } } }


  &__btn {
    margin-left: 0; }


  &__img {
    display: block;
    max-width: 100%;
    height: auto;
    margin-left: auto;
    margin-right: auto;
    @include r(575) {
      max-width: 100%; }

    &--des {
      display: none;
      @include r(767) {
        max-width: 70%; }
      @include rmin(576) {
        display: block; } }
    &--mob {
      display: none;
      @include r(575) {
        display: block;
        margin-top: 30px;
        width: 100%;
        object-fit: contain; } } } }


// \ INVESTORS
