// RISKS

.risks {
  padding-top: 52px;
  padding-bottom: 68px;
  @include r(767) {
    padding-top: 26px;
    padding-bottom: 34px; }


  &__title {
    padding-bottom: 40px;
    font-size: 18px;
    font-weight: 600;
    @include r(767) {
      padding-bottom: 20px; } }


  &__block {
    display: flex;
    padding: 17px 20px;
    margin-bottom: 20px;
    background-color: $c-whiteSmoke;
    @include r(767) {
      flex-direction: column; }

    &:last-child {
      margin-bottom: 0; }

    &-left {
      @include rmin(320) {
        width: 100%; }
      @include rmin(768) {
        width: 308px; }
      @include rmin(992) {
        width: 408px; }

      p {
        font-size: 18px;
        @include r(767) {
          padding-bottom: 20px;
          font-size: 16px; } } }

    &-right {
      width: calc(100% - 408px);
      @include rmin(320) {
        width: 100%; }
      @include rmin(768) {
        width: calc(100% - 308px); }
      @include rmin(992) {
        width: calc(100% - 408px); }
      @include rmin(1200) {
        padding-right: 105px; }

      p {
        font-size: 18px;
        font-weight: 700;
        @include r(767) {
          font-size: 16px; } }

      ul {
        padding-top: 20px;

        li {
          padding-bottom: 10px;
          @include rmin(768) {
            font-size: 16px; } } } } } }

// \ RISKS
