// BLOGS
.blogs {
  padding-top: 144px;
  padding-bottom: 143px;
  @include r(767) {
    padding-top: 70px;
    padding-bottom: 70px; }

  .blog &,
  .blogDetails & {
    @include r(767) {
      .swiper-wrapper {
        flex-wrap: wrap;
        height: auto !important; } } }

  .blog & {
    padding-top: 55px;
    padding-bottom: 128px;
    @include r(767) {
      padding-top: 30px;
      padding-bottom: 64px; } }

  .blogDetails & {
    padding-bottom: 125px;
    @include r(767) {
      padding-bottom: 64px; } }


  &__title {
    padding-bottom: 58px;
    @include r(767) {
      padding-bottom: 29px; } }


  &__subtitle {
    font-weight: 600;
    padding-bottom: 32px;
    @include rmin(320) {
      font-size: 20px; }
    @include rmin(768) {
      font-size: 22px; }
    @include rmin(992) {
      font-size: 24px; }
    @include rmin(1200) {
      font-size: 28px; } }


  &__pagination {
    padding-bottom: 12px;

    &-list {
      display: flex;
      flex-wrap: wrap; }

    &-item {
      display: flex;
      padding-bottom: 10px; }

    &-link {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 32px;
      border-radius: 3px;
      border: 1px solid rgba($c-royalBlue, 0.2);
      margin-right: 10px;
      padding-left: 10px;
      padding-right: 10px;
      color: $c-royalBlue;
      text-align: center;
      font-size: 18px;
      @include transition(border-color, background-color, color);
      @include r(767) {
        font-size: 16px; }

      &:hover {
        border-color: $c-royalBlue;
        @include transition(border-color);
        transition-duration: 0s; }
      &.is-active {
        border-color: transparent;
        background-color: rgba($c-blueZodiac, 0.24);
        color: $c-blueZodiac;
        @include transition(border-color, background-color, color);
        transition-duration: 0s; } } }


  &__block {
    white-space: normal;
    width: 388px;
    margin-bottom: 20px;
    border-radius: 3px;
    border: 1px solid $c-whisper;
    background-color: $c-white;
    @include transition(box-shadow);
    @include r(767) {
      .homepage & {
        flex: 0 0 300px;
        margin-right: 18px;
        margin-bottom: 50px; }

      position: static !important;
      width: calc((100% - 36px) / 2);
      margin-right: 18px;
      margin-bottom: 18px; }
    @include r(575) {
      width: 100%;
      margin-left: auto;
      margin-right: auto; }
    @include rmin(768) {
      display: block;
      position: absolute !important;
      margin-right: 0;
      width: calc((100% - 18px) / 2); }
    @include rmin(992) {
      width: calc((100% - 36px) / 3); }

    &:hover {
      box-shadow: 0 0 15px 0 rgba($c-blueZodiac, 0.1);
      @include transition(box-shadow);
      transition-duration: 0s;
      .blogs__block-img {
        transform: scale(1.1);
        @include transition(transform);
        transition-duration: 0s; } }

    &.is-hide {
      display: none; }

    &--image {
      .blogs__block-header {
        padding: 0;

        p {
          z-index: 1;
          position: relative; } } }

    &-header {
      padding: 30px 25px 18px;

      p {
        font-size: 20px;
        font-weight: 600; } }

    &-body {
      padding: 20px 25px;

      p {
        font-size: 16px; } }

    &-footer {
      display: flex;
      align-items: center;
      padding: 0 25px 20px;
      @include rmin(320) {
        align-items: flex-start;
        flex-direction: column; }
      @include rmin(1200) {
        align-items: center;
        flex-direction: row; }

      &--left {
        width: 75%;
        @include rmin(320) {
          width: 100%; }
        @include rmin(1200) {
          width: 75%; } }

      &--right {
        width: calc(100% - 75%);
        @include rmin(320) {
          width: 100%; }
        @include rmin(1200) {
          width: calc(100% - 75%); } } }

    &-wrap {
      overflow: hidden;
      position: relative;
      display: flex;
      align-items: flex-end;
      height: 257px;
      padding: 30px 25px 0;
      background-repeat: no-repeat;
      background-size: cover;

      &:after {
        content: "";
        z-index: 0;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: -moz-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);
        background: -webkit-linear-gradient(top, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
        background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ffffff',GradientType=0 ); } }

    &-badge {
      display: flex;
      flex-wrap: wrap;

      p {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 24px;
        padding-left: 5px;
        padding-right: 5px;
        margin-right: 9px;
        border-radius: 3px;
        border: 1px solid $c-whisper;
        color: $c-royalBlue;

        &:last-child {
          p {
            margin-right: 0; } }

        &:nth-child(n + 3) {
          margin-top: 9px; } }

      span {
        color: $c-nobel; } }

    &-date {
      white-space: nowrap;
      color: $c-nobel;
      font-size: 12px;
      @include rmin(320) {
        margin-top: 9px; }
      @include rmin(1200) {
        margin-top: 0; } }

    &-img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      @include transition(transform); } }


  &__btn {
    margin-top: 62px;
    @include r(767) {
      margin-top: 31px; }

    &--blog {
      margin-top: 72px;
      @include r(767) {
        margin-top: 36px; } }
    &--blogDetails {
      margin-top: 25px; } } }
// \ BLOGS
