// Styles for dialog window
.mfp-wrap {
  -webkit-overflow-scrolling: touch;
  -webkit-transform: translateZ(0); }

html.mfp-helper {
  height: 100%;
  body {
    overflow: hidden;
    height: 100%;
    -webkit-transform: translateZ(0); } }

#small-dialog {
  background: white;
  padding: 20px 30px;
  text-align: left;
  max-width: 600px;
  margin: 40px auto;
  position: relative; }


// Fade-zoom animation for first dialog

.popup-buble {
  .popup-dialog {
    opacity: 0;
    transition: all 0.2s ease-in-out;
    transform: scale(0.8); }
  &.mfp-ready .popup-dialog {
    opacity: 1;
    transform: scale(1); }
  &.mfp-removing .popup-dialog {
    transform: scale(0.8);
    opacity: 0; }
  &.mfp-bg {
    background: $colorGreen;
    opacity: 0;
    transition: opacity 0.3s $ease; }
  &.mfp-ready.mfp-bg {
    opacity: 1; }
  &.mfp-removing.mfp-bg {
    opacity: 0; } }



// Fade-move animation
.my-mfp-slide-bottom {
  .zoom-anim-dialog {
    opacity: 0;
    transition: all 0.2s ease-out;
    transform: translateY(-20px) perspective(600px) rotateX(10deg); }
  &.mfp-ready .zoom-anim-dialog {
    opacity: 1;
    transform: translateY(0) perspective(600px) rotateX(0); }
  &.mfp-removing .zoom-anim-dialog {
    opacity: 0;
    transform: translateY(-10px) perspective(600px) rotateX(10deg); }
  &.mfp-bg {
    opacity: 0;
    transition: opacity 0.3s ease-out; }
  &.mfp-ready.mfp-bg {
    opacity: 0.8; }
  &.mfp-removing.mfp-bg {
    opacity: 0; } }

.popup-buble {
  .mfp-close {
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 10000;
    opacity: 1;
    width: auto;
    height: auto;
    font-size: 50px;
    padding: 10px;
    .ico {
      font-size: 20px;
      transition: .25s $ease;
      fill: white; } }
  .popup-dialog, .mfp-content {
    opacity: 0;
    transition: all 0.2s ease-in-out;
    transform: scale(0.8) translate3d(0,50%,0); }
  &.mfp-ready {
    .popup-dialog, .mfp-content {
      opacity: 1;
      transform: scale(1); } }
  &.mfp-removing {
    .popup-dialog, .mfp-content {
      transform: scale(0.8) translate3d(0,-50%,0);
      opacity: 0; } }
  &.mfp-bg {
    background: rgba($c-blueZodiac, .99);
    opacity: 0;
    transition: opacity 0.3s $ease; }
  &.mfp-ready.mfp-bg {
    opacity: 1; }
  &.mfp-removing.mfp-bg {
    opacity: 0; } }


.mfp-iframe-holder {
  position: relative;
  width: 100%;
  max-width: 790px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 30px;
  padding-right: 30px;
  @include r(767) {
    padding-left: 20px;
    padding-right: 20px; }
  .mfp-content {
    //max-width: 100%
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 445px;
    margin: auto;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0; } }
