// GUARANTEES

.guarantees {
  padding-top: 68px;
  padding-bottom: 78px;
  @include r(767) {
    padding-top: 34px;
    padding-bottom: 39px; }

  .investors & {
    padding-top: 103px;
    padding-bottom: 75px;
    @include r(767) {
      padding-top: 51px;
      padding-bottom: 37px; } }

  .cabinet & {
    padding-top: 0;
    padding-bottom: 181px;
    @include r(767) {
      padding-bottom: 90px; } }


  &__title {
    padding-bottom: 61px;
    @include r(767) {
      padding-bottom: 30px; } }


  &__row {
    display: flex;
    @include r(575) {
      flex-direction: column; } }


  &__box {
    width: 50%;
    padding: 34px 30px;
    background-color: $c-whiteSmoke;
    @include r(575) {
      width: 100%; }

    &-0 {
      img {
        width: 100%;
        max-width: 129px;
        height: 100%;
        max-height: 156px; } }

    &-1 {
      img {
        width: 100%;
        max-width: 284px;
        height: 100%;
        max-height: 132px; } }

    &:nth-child(1) {
      margin-right: 10px;
      @include r(575) {
        margin: 0;
        margin-bottom: 20px; } }

    &:nth-child(2) {
      margin-left: 10px;
      @include r(575) {
        margin: 0; } }


    &-wrap {
      height: 156px;
      margin-bottom: 15px;

      .ico-img-box-0__guarantees {
        width: 129px;
        height: 156px; }

      .ico-img-box-1__guarantees {
        width: 282px;
        height: 128px; } }


    &-img {
      display: block;
      max-width: 100%; }


    &-title {
      padding-bottom: 17px;
      font-weight: 600;
      @include rmin(320) {
        font-size: 22px; }
      @include rmin(768) {
        font-size: 25px; }
      @include rmin(992) {
        font-size: 28px; } }


    &-text {
      @include r(575) {
        br {
          display: none; } }
      @include rmin(320) {
        font-size: 14px; }
      @include rmin(768) {
        font-size: 16px; }
      @include rmin(992) {
        font-size: 18px; } } } }


// \ GUARANTEES
