        padding-top: 37px;
        padding-bottom: 125px; }

      .main__subtitle {
        width: 100%;
        max-width: 933px; } }

    &__title {
      padding-bottom: 38px;
      @include r(767) {
        padding-bottom: 20px; } } } }


