// QUESANS

.quesAns {
  padding-top: 90px;
  padding-bottom: 143px;
  @include r(767) {
    padding-top: 45px;
    padding-bottom: 70px; }

  .swiper-wrapper {
    @include r(767) {
      flex-wrap: wrap;
      height: auto !important; } }


  &__pagination {
    padding-bottom: 12px;

    &-list {
      display: flex;
      flex-wrap: wrap; }

    &-item {
      display: flex;
      padding-bottom: 10px; }

    &-link {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 32px;
      border-radius: 3px;
      border: 1px solid rgba($c-royalBlue, 0.2);
      margin-right: 10px;
      padding-left: 10px;
      padding-right: 10px;
      color: $c-royalBlue;
      text-align: center;
      font-size: 18px;
      @include transition(border-color, background-color, color);
      @include r(767) {
        font-size: 16px; }

      &:hover {
        border-color: $c-royalBlue;
        @include transition(border-color);
        transition-duration: 0s; }
      &.is-active {
        border-color: transparent;
        background-color: rgba($c-blueZodiac, 0.24);
        color: $c-blueZodiac;
        @include transition(border-color, background-color, color);
        transition-duration: 0s; } } }


  &__block {
    white-space: normal;
    width: 388px;
    margin-bottom: 20px;
    padding: 30px 25px 35px;
    border-radius: 3px;
    border: 1px solid $c-whisper;
    background-color: $c-white;
    @include transition(box-shadow);
    @include r(767) {
      position: static !important;
      width: calc((100% - 36px) / 2);
      margin-right: 18px;
      margin-bottom: 18px; }
    @include r(575) {
      width: 100%;
      margin-left: auto;
      margin-right: auto; }
    @include rmin(768) {
      display: block;
      margin-right: 0;
      width: calc((100% - 18px) / 2); }
    @include rmin(992) {
      width: calc((100% - 36px) / 3); }

    &:hover {
      box-shadow: 0 0 15px 0 rgba($c-blueZodiac, 0.1);
      @include transition(box-shadow);
      transition-duration: 0s; }

    &.is-hide {
      display: none; }

    &-header {
      p {
        font-size: 20px;
        font-weight: 600; } }

    &-body {
      p {
        font-size: 16px; } } }


  &__btn {
    margin-top: 62px;
    @include r(767) {
      margin-top: 31px; } } }


// \ QUESANS
