// ADVICE

.advice {
  padding-top: 69px;
  padding-bottom: 82px;
  background-color: $c-whiteSmoke;
  @include r(767) {
    padding-top: 34px;
    padding-bottom: 41px; }


  &__title {
    padding-bottom: 63px;
    @include r(767) {
      padding-bottom: 31px; } }


  &__row {
    display: flex;
    flex-wrap: wrap;
    @include r(767) {
      .form__field {
        max-width: 100%; } } }


  &__col {
    width: calc(100% / 3);
    @include r(767) {
      width: 100%; }
    @include rmin(768) {
      &:nth-child(1),
      &:nth-child(4) {
        padding-right: 10px; }

      &:nth-child(2),
      &:nth-child(5) {
        padding-left: 10px;
        padding-right: 10px; }

      &:nth-child(3),
      &:nth-child(6) {
        padding-left: 10px; } }

    @include rmin(1200); }


  &__btn {
    margin-left: 0;
    @include r(767) {
      margin-top: 15px;
      margin-left: auto; } } }

// \ ADVICE
