// TABS

.howWeEarn {
  .tabs {
    padding-top: 60px;
    padding-bottom: 124px;
    @include r(767) {
      padding-top: 30px;
      padding-bottom: 62px; }


    &__container {
      display: none;

      &.is-active {
        display: block;
        animation-name: fadeIn;
        animation-duration: 1.25s; } }


    &__subtitle {
      width: 100%;
      max-width: 880px;
      padding-bottom: 44px;
      @include r(767) {
        padding-bottom: 22px; } }

    &__text {
      width: 100%;
      max-width: 753px;
      padding-bottom: 82px;
      @include r(767) {
        padding-bottom: 41px; } }

    &__title {
      font-size: 28px;
      font-weight: 600;
      padding-bottom: 49px;
      @include r(767) {
        padding-bottom: 24px; } }


    &__row {
      display: flex;
      flex-wrap: wrap; }


    &__col {
      margin-bottom: 20px;
      @include rmin(320) {
        width: 100%; }
      @include rmin(768) {
        width: 50%;
        padding-right: 50px; }
      @include rmin(992) {
        padding-right: 90px; }
      @include rmin(1200) {
        padding-right: 120px; }

      p {
        &:nth-child(1) {
          font-size: 20px;
          font-weight: 600;
          padding-bottom: 28px;
          @include r(767) {
            font-size: 18px; } }

        &:nth-child(2) {
          font-size: 18px;
          @include r(767) {
            font-size: 16px; } } } } } }


